import Page from 'flarum/components/Page';
import IndexPage from 'flarum/components/IndexPage';
import listItems from 'flarum/common/helpers/listItems';
import Button from 'flarum/components/Button';
import Stream from 'flarum/utils/Stream';

export default class UserSelfQueryIndexPage extends Page {
  oninit(vnode) {
    super.oninit(vnode);
    this.bodyClass = 'App--index';
    this.phone = Stream("");
    this.paused = false;
    this.maxCount = 1986;
    this.counter = 0;
    this.thread = {};

    const urlListSetting = app.forum.attribute("userSelfQueryUrl") || "";
    const urlListSplit = urlListSetting.split(",");
    this.urlList = this.shuffle(urlListSplit);

  }

  view() {
    return (
      <div className="IndexPage">
        {IndexPage.prototype.hero()}

        <div className="container">
          <div className="sideNavContainer">
            <nav className="IndexPage-nav sideNav">
              <ul>{listItems(IndexPage.prototype.sidebarItems().toArray())}</ul>
            </nav>

            <div style="padding: 25px 0px 0px 0px;width: 100%;">
              <div style="padding-bottom: 20px;position:relative">
                <div style="display: inline-block;width: 80%;"><input type="number" placeholder="请输入查询的手机号" className="FormControl" bidi={this.phone} /></div>
                <div>
                  {Button.component({
                      id: 'userSelfQueryStart',
                      style:'width: 66px;position: absolute;top: 0px;right: 70px;font-weight: normal !important;background: rgb(230, 96, 27) !important;',
                      className: 'Button Button--primary',
                      onclick: () => {
                        this.start();
                      }
                    },
                    "开始"
                  )}

                  {Button.component({
                      id: 'userSelfQueryPause',
                      style:'display:none;width: 66px;display:none;position: absolute;top: 0px;right: 70px;font-weight: normal !important;',
                      className: 'Button',
                      onclick: () => {
                        this.pause();
                      }
                    },
                    "暂停"
                  )}

                  {Button.component({
                      style:'display:none;width: 66px;position: absolute;top: 0px;right: 70px;font-weight: normal !important;background: rgb(230, 96, 27) !important;',
                      id: 'userSelfQueryResume',
                      className: 'Button Button--primary',
                      onclick: () => {
                        this.resume();
                      }
                    },
                    "继续"
                  )}

                  {Button.component({
                    style:'width: 66px;position: absolute;top: 0px;right: 0px;font-weight: normal !important;',
                    className: 'Button Button--primary',
                    onclick: () => {
                      this.copyLog();
                    }
                  },
                  "复制"
                )}
                </div>
              </div>


              <div>
                <div id="userSelfQueryLogs" style="border:1px solid rgba(255,255,255,.2);height: 400px;overflow-y: auto;text-align: left;padding: 15px;border-radius: 4px;">
                </div>
              </div>

              <div style="text-align:center;padding-top: 10px;">
                <div>当前0.5测试版存在BUG 正在更新</div>
                <div>开发0.8更新后使用反馈投稿社区留言致电</div>
              </div>

            </div>
          </div>
        </div>
      </div>
    );
  }

  clearLog(){
    $("#userSelfQueryLogs").empty();
  }

  copyLog(){
    var range = document.createRange();
    range.selectNode(document.getElementById("userSelfQueryLogs"));
    window.getSelection().removeAllRanges(); 
    window.getSelection().addRange(range); 
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    alert("已复制日志");
  }

  pause(){
    this.paused = true;
    $("#userSelfQueryLogs").append("<div>已暂停</div><div><hr style='border-top:1px solid #313952' />");
    $("#userSelfQueryPause").hide();
    $("#userSelfQueryResume").show();
  }

  resume(){
    this.paused = false;
    $("#userSelfQueryLogs").append("<div>已恢复查询</div><div><hr style='border-top:1px solid #313952' />");
    $("#userSelfQueryPause").show();
    $("#userSelfQueryResume").hide();
  }

  start(){
    let phone = this.phone();
    let checkPhone = this.checkMobile(phone);

    if(checkPhone===false){
      alert("请输入正确的手机号码")
    }else{
      this.clearLog();
      let threadID = Object.keys(this.thread).length;
      this.runQuery(threadID,500);
      $("#userSelfQueryPause").show();
      $("#userSelfQueryStart").hide();
    }
  }

  runQuery(threadID,delay){
    let _this = this;

    if(this.thread[threadID]===undefined){
      this.thread[threadID] = 0;
    }

    setTimeout(function(){
      if(_this.paused===true){
        _this.runQuery(threadID);
      }else{
        let counter = _this.thread[threadID];
        let seed = _this.randomInt(1,4);
        $("#userSelfQueryLogs").append(_this.getResult(counter,seed));
        $('#userSelfQueryLogs').scrollTop(99999999);

        if(counter===_this.maxCount){
          $("#userSelfQueryStart").show();
          $("#userSelfQueryPause").hide();
          $("#userSelfQueryResume").hide();
        }else{
          _this.thread[threadID]++;
          _this.runQuery(threadID);
        }
      }
    },delay!==undefined?delay:_this.randomInt(3000,4000));
  }

  getResult(counter,seed){
    let text = "<div>"+new Date().toLocaleString('sv-SE')+" 正在查询"+(counter+1)+"个</div>";

    if(counter===this.maxCount){
      text = "<div>查询完毕感谢您的使用</div><div>反馈相关事宜投稿站点请添加POP群组</div>";
    }else{
      if(seed===2 && this.urlList.length===0){
        seed = 1;
      }

      if(seed===1){
        text+="<div style='color:white'>您的手机号暂未收到邀请，谢谢</div>";
      }else if(seed===2){
        text+="<a target='_blank' href='"+this.urlList[0]+"'><div style='color:green'>"+this.urlList[0]+"</div>";
        text+="<div style='color:green'>您是尊贵特邀会员，谢谢</div>";
        text+="<div style='color:green'>请尽快前往特邀网址，避免过期</div></a>";
        this.urlList.shift();
      }else if(seed===3){
        text+="<div style='color:red'>您的手机号并非特邀会员，请勿重复提交</div>";
      }else if(seed===4){
        text+="<div style='color:white'>您的手机号已提交申请彩金</div>";
        text+="<div style='color:white'>信息请勿重复提交</div>";
      }
    }

    text+="<div><a target='_blank' href='https://老哥.com'>https://老哥.com 免费供应技术支持</a></div>";
    text+="<div><hr style='border-top:1px solid #313952' /></div>";

    return "<div>"+text+"</div>";
  }

  randomInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }

  shuffle(array) {
    let currentIndex = array.length,  randomIndex;

    while (currentIndex != 0) {
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex--;

      [array[currentIndex], array[randomIndex]] = [
        array[randomIndex], array[currentIndex]];
    }

    return array;
  }

  checkMobile(mobile) {
    var re = /^1[3,4,5,6,7,8,9][0-9]{9}$/;
    var result = re.test(mobile);

    if(!result) {
      return false;
    }else{
      return true;
    }
  }

}
